<script lang="ts">
import { defineComponent } from 'vue'
import { RouteLocationMatched, RouteLocationNormalized } from 'vue-router'

export default defineComponent({
    computed:{
        isLogged():boolean{
            return this.$route.matched.some((route: RouteLocationMatched) => route.name === 'profile')
        },
        btnText():string{
            return !this.isLogged? 'sell' : '+ add';
        },
        isCurrPageLogin():string{
            return this.$route.name === 'login'? 'register' : 'login'
        }
    }
})
</script>

